@import url(https://fonts.googleapis.com/css2?family=Bungee+Inline&family=Special+Elite&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{
  box-sizing: border-box;
}
.App {
  display: block;
  margin-right: auto;
  margin-left: auto;
  background-color: black;
  height: 100vh;
  width: 100vw;
}
h1 {
  color: white;
  text-align: center;
  font-family: 'Special Elite', cursive;
  margin-top: 0px;
  padding-top: 20px;
  padding-left: 10px;
  padding-right: 10px;
}
.node {
  height: 10px;
  width: 10px;
}
.buttonsContainer {
  margin-right: auto;
  margin-left: auto;
  text-align: center;
}
button {
  border-radius: 4px;
  background-color: black;
  border: 2px solid white;
  color: white;
  margin: 2px;
  font-weight: bold;
  font-size: .9rem;
  font-family: 'Bungee Inline', cursive;
  cursor: pointer;
}
button:hover {
  background-color: white;
  color: black;
}
button:active {
  background-color: gray;
  transform: translateY(4px);
}
.gridContainer {
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  margin-left: auto;
  margin-right: auto;
  margin-top: 40px;
}


@media screen and (min-width: 500px) {
  .buttonsContainer {
    margin-right: auto;
    margin-left: auto;
    width: 500px;
    text-align: center;
  }
}
